<template>
    <div class="random-links">
        <div v-if="!isShow">
            <el-button type="success" @click="add()">添加新链接</el-button>
            <el-table :data="tableData" style="width: 100%;" border class="wrapper">
                <el-table-column prop="id" label="url">
                    <template slot-scope="scope">
                        <span>http://www.baidu.com/{{scope.row.id}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="title" label="title"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">							
                        <el-button size="small" type="primary" @click="update(scope.row)">修改</el-button>
                        <el-button size="small" type="danger" @click="deleteOne(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="form-con" v-else>
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="" prop="id">
                    <h2>添加/修改链接</h2>
                </el-form-item>
                <el-form-item label="id" prop="id">
                    <el-input style="width: 200px;"  prop="id" ref="id" v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item label="title" prop="title">
                    <el-input style="width: 400px;"  prop="title" ref="title" v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="link" prop="link">
                    <el-input style="width: 600px;"  prop="link" ref="link" v-model="link"></el-input>
                    <el-button style="margin-left: 20px;" type="primary" @click="addOne">添加</el-button>
                </el-form-item>
                <el-form-item label="links" prop="links">
                    <ul>
                        <li v-for="(item, index) in form.links" :key="index">
                            <span>{{item}}</span>
                            <el-button type="danger" @click="deleteLink(index)">X</el-button>
                        </li>
                    </ul>
                </el-form-item>
                <el-form-item prop="links">
                    <el-button v-if="isUpdate" type="primary" @click="save()">保存</el-button>
                    <el-button v-else type="primary" @click="create()">创建</el-button>
                    <el-button type="info" @click="cancel()">取消</el-button>
                </el-form-item>
            </el-form>
        </div>
        
    </div>
</template>

<script>
    import Axios from 'axios';
    export default {
        data() {
            return {
                form: {
                    links: []
                },
                tableData: [],
                link: '',
                isShow: false,
                isUpdate: false
            }
        },
        mounted() {
            this.showList()
        },
        methods: {
            add() {
                this.isShow = true
            },
           update(row) {
                this.isShow = true
               this.isUpdate = true
               this.form = row
           },
           deleteOne(row) {
               Axios({
                   url: '/transfer/delete',
                   method: 'get',
                   params: {
                       id: row._id
                   }
               }).then(res => {
                   this.$message({
                       type: 'success',
                       message: res.data.success
                   })
                   this.showList()
               })
           },
           deleteLink(index) {
               this.form.links.splice(index, 1)
           },
           addOne() {
               if (this.link == '') {
                    this.$message.error('链接不能为空')
               } else {
                    this.form.links.push(this.link)
                    this.link = ''
               }
           },
           save() {
               Axios({
                   url: '/transfer/update',
                   method: 'post',
                   data: {
                       _id: this.form._id,
                       id: this.form.id,
                       title: this.form.title,
                       links: this.form.links
                   }
               }).then(res => {
                   if(res.data.success) {
                       this.$message({
                           type: 'success',
                           message: res.data.success
                       })
                       this.isShow = this.isUpdate =  false
                       this.showList()
                       this.form = ''
                   } else {
                       this.$message.error(res.data.err)
                   }
               })
           },
           create() {
               Axios({
                   url: '/transfer/create',
                   method: 'post',
                   data: {
                       id: this.form.id,
                       title: this.form.title,
                       links: this.form.links
                   }
               }).then(res => {
                   if (res.data.success) {
                       this.$message({
                           type: 'success',
                           message: res.data.success
                       })
                   } else {
                       this.$message.error(res.data.err)
                   }
               })
               this.form = {}
               this.isShow = false
           },
           cancel() {
               this.form = {}
               this.isUpdate = false
               this.isShow =false
           },
           showList() {
               Axios({
                   url: '/transfer',
                   method: 'get'
               }).then(res => {
                   this.tableData = res.data.messages
               })
           }
        }
    }
</script>


<style scoped>
.wrapper {
    margin: 30px 0;
}

.form-con {
    border: 1px solid #eee;
    padding-top: 20px;
}
li {
    float: left;
    margin: 0 50px 20px 0;
}
li span {
    display: inline-block;
    padding: 0 15px;
    border: 1px solid #eee;
    border-radius: 5px;
    margin-right: 10px;
}
</style>



